@import '../../scss/mixin';

.preview__options{
  @include flex-row;
  @include justify-start;
  margin-bottom: 10px;

  .preview__option{
    @include display-block;
    width: max-content;
    background: #222227;
    border-radius: 6px;
    border: solid 1px #666;
    margin-right: 8px;

    input{
      @include display-block;
      visibility: hidden;
      width: 0;
      height: 0;
    }
    label,button{
      @include display-block;
      width: auto;
      padding: 8px;
    }
    svg{
      display: block;
      height: 18px;
      width: 18px;
      path{
        stroke:#aaa;
        fill:#aaa;
      }
    }
    &:nth-child(2) svg path{
      fill: none;
    }
    &:hover{
      svg path{
        stroke:#fff;
        fill:#fff;
      }
      &:nth-child(2) svg path{
        fill: none;
      }
    }
  }
}

.canvas__container{
  @include display-block;
  border: dotted 2px #32CCBC;

  .canvas__preview{
    @include display-block;
    background: #fff;
    margin: 0 auto;
    overflow: hidden;

    img{
      display: block;
      position: absolute;
      object-fit: contain;
    }

    .canvas__border{
      --b :10px;
      @include display-block;
      position: absolute;
      inset: 0;
      z-index: 10;
      span{
        @include display-block;
        position: absolute;
        height: 100%;
        background: #fff;

        &:nth-of-type(1){
          top:0;
          height: var(--b);
        }
        &:nth-of-type(2){
          bottom:0;
          height: var(--b);
        }
        &:nth-of-type(3){
          top:0;
          width: var(--b);
        }
        &:nth-of-type(4){
          right:0;
          width: var(--b);
        }
      }
    }
  }
}
