
@import '../../scss/mixin';



.file__upload__wrap{
  @include display-block;
  cursor: pointer;
  border: dotted 2px #32CCBC;
  border-radius: 10px;
  padding: 30px;
  max-width: 600px;

  &:hover{
    border-color: #90F7EC;
  }

  input{
    @include display-block;
    visibility: hidden;
    width: 0;
    height: 0;
  }
  label{
    @include flex-column;
    @include align-center;

    svg{
      @include display-block;
      width: 150px;
      height: auto;
      path{
        stroke: #ccc;
      }
    }

    p{
      @include display-block;
      width: auto;
      font: 400 18px/22px 'Inter', sans-serif;
      color: #ccc;
      text-decoration: underline #ccc;
    }
  }
}





.multi__files__display{
  @include flex-row;
  @include justify-start;
  flex-wrap: wrap;
  border: dotted 2px #32CCBC;
  padding:5px;

  .multi__files__display__each{
    @include display-block;
    width: auto;
    border: solid 2px #888;
    width: 20%;
    &:after{
      @include display-block;
      content: "";
      padding-top: 100%;
    }
    &:last-child{
      margin-right: 0;
    }

    img{
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    span{
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      border-radius: 50%;
      padding: 1px;
      border: solid 1px #000;
      background: #32CCBC;
      cursor: pointer;
      z-index: 1;
      svg{
        display: block;
        width: 16px;
        height: 16px;
        fill:#000;
      }
      &:hover{
        border-color: #32CCBC;
        svg{fill:#fff;}
      }
    }

    input{
      @include display-block;
      visibility: hidden;
      width: 0;
      height: 0;
    }
    label{
      @include flex-row;
      position: absolute;
      inset: 0;
      background: #222227;
      cursor: pointer;
      z-index: 1;
      svg{
        display: block;
        position: relative;
        width: 50px;
        height: 50px;
        fill:#ccc;
        transform: rotate(45deg);
      }
      &:hover{
        svg{fill:#32CCBC;}
      }
    }

    @media(max-width:768px){
      width: 25%;
      label{
        svg{
          width: 40px;
          height: 40px;
        }
      }
    }
  }
}
