@import '../../scss/mixin';

/***********************Start home__banner********************************/

.family__container{
  @include flex-row;
  @include justify-start;
  flex-wrap: wrap;
  padding-top: 50px;
  background: transparent;

  p{
    @include display-block;
    width: auto;
    color: #fff;
    background: #f00;
    margin: 20px;
    padding: 4px 15px;
    border-radius: 4px;
    font-weight: 600;
    font-size: 24px;

    &.level-1{
      background: #e00;
      color: #ffe;
    }
    &.level-2{
      background: #d00;
      color: #ffd;
    }
    &.level-3{
      background: #c00;
      color: #ffc;
    }
    &.level-4{
      background: #b00;
      color: #ffb;
    }
    &.level-5{
      background: #a00;
      color: #ffa;
    }
    &.level-6{
      background: #900;
      color: #ff9;
    }
    &.level-7{
      background: #800;
      color: #ff8;
    }
    &.level-8{
      background: #700;
      color: #ff7;
    }
  }


}
/***********************End home__banner********************************/
