
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&family=Oswald:wght@400;500;600&family=Spline+Sans:wght@400;500;600&family=Ubuntu:ital,wght@0,400;0,500;1,400&family=Work+Sans:wght@400;500;700&display=swap');
@import 'mixin';


#root{
  position: relative;
  display: block;
}


.main {
  @include display-block;
}


.section__model{
  @include display-block;
  &.section__model_row{
    @include flex-row;
    @include align-start;
    margin: 0 auto;
  }
  &.main__margin{
    margin-top: 40px;
  }
  max-width: 1300px;
  margin: 0 auto;

  .section__model_half{
    @include display-block;
    width: 50%;
    &:last-child{
      padding-left: 60px;
    }
  }

  @media(max-width:1440px){
    max-width: 1180px;
  }
  @media(max-width:1200px){
    max-width: 992px;
    .section__model_half{
      &:last-child{
        padding-left: 40px;
      }
    }
  }
  @media(max-width:992px){
    max-width: inherit;
    padding: 0 25px;

    &.section__model_row{
      flex-wrap: wrap;
    }
    .section__model_half{
      width: 70%;
      &:last-child{
        padding-left: 0;
      }
    }
  }
  @media(max-width:768px){
    padding: 0 20px;
    &.section__model_row{
      padding: 0 20px;
    }
    &.main__margin{
      margin-top: 30px;
    }
    .section__model_half{
      width: 100%;
    }
  }
}


.ball__pulse{
  margin-left: 8px;
  div{
    display: inline-block;
    background-color: #fff;
    width: 7px;
    height: 7px;
    border-radius: 100%;
    margin: 2px;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;

    &:nth-child(1) {
      -webkit-animation: scale 0.75s -0.24s infinite cubic-bezier(0.2, 0.68, 0.18, 1.08);
              animation: scale 0.75s -0.24s infinite cubic-bezier(0.2, 0.68, 0.18, 1.08);
            }

    &:nth-child(2) {
      -webkit-animation: scale 0.75s -0.12s infinite cubic-bezier(0.2, 0.68, 0.18, 1.08);
              animation: scale 0.75s -0.12s infinite cubic-bezier(0.2, 0.68, 0.18, 1.08);
            }
    &:nth-child(3) {
      -webkit-animation: scale 0.75s 0s infinite cubic-bezier(0.2, 0.68, 0.18, 1.08);
              animation: scale 0.75s 0s infinite cubic-bezier(0.2, 0.68, 0.18, 1.08);
            }
  }
}

@keyframes scale {
0% {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1; }
45% {
  -webkit-transform: scale(0.1);
          transform: scale(0.1);
  opacity: 0.7; }
80% {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1; } }
