
@import '../../scss/mixin';

.crop__container{
  position: absolute;
  display: block;
  inset:0;

  .crop__shadow{
    content: "";
    position: absolute;
    display: block;
    inset: 0;
    background: transparent;
    @include user-select-none;
    z-index: 1;
    .top__shadow,.bottom__shadow,.right__shadow,.left__shadow{
      content: "";
      position: absolute;
      display: block;
      background: rgba(0,0,0,0.7);
      z-index: 1;
    }
    .top__shadow{
      left: 0;
      top: 0;
      height: 0;
      width: 0;
    }
    .bottom__shadow{
      top: 100%;
      left: 0;
      bottom: 0;
      width: 0;
    }
    .left__shadow{
      top: 0;
      bottom: 0;
      width: 0;
    }
    .right__shadow{
      top: 0;
      bottom: 0;
      right: 0;
      left: 100%;
    }
  }

  .crop__box{
    --iw :40px;
    position: absolute;
    display: block;
    margin: 0;
    padding: 0;
    width: 200px;
    height: 200px;
    top: 0;
    left:0;
    background: transparent;
    z-index: 2;

    @include user-select-none;

    &:after{
      content: "";
      position: absolute;
      display: block;
      inset:0;
      border: solid 1px #ccc;
      box-shadow: inset 0 0 3px 0 #000;
    }

    .move{
      @include flex-row;

      position: absolute;
      inset:0;
      cursor: move;
      z-index: 1;
      color: #000;
      font-size: 0;
      &.active{
        font: 400 30px/15px 'Inter', sans-serif;
      }
    }

    .top,.left,.right,.bottom{
      position: absolute;
      display: block;
      margin: 0;
      padding: 0;
      background: #fff;
      border-radius: 3px;
      z-index: 1;
      transition: none;
      &.active{
        background: #32CCBC;
      }
    }
    .top,.bottom{
      width: var(--iw);
      height: 3px;
      left: calc(50% - calc(var(--iw)/2));
      top: -1px;
      cursor: n-resize;
    }
    .bottom{
      top: initial;
      bottom: -1px;
    }
    .left,.right{
      height: var(--iw);
      width: 3px;
      top: calc(50% - calc(var(--iw)/2));
      left: -1px;
      cursor: e-resize;
    }
    .right{
      left: initial;
      right: -1px;
    }


    .top_left,.top_right,.bottom_left,.bottom_right{
      position: absolute;
      display: block;
      margin: 0;
      padding: 0;
      background: #fff;
      border-radius: 3px;
      z-index: 1;
      width: calc(var(--iw)/2);
      height: 3px;
      transition: none;
      &:after{
        content: "";
        position: absolute;
        display: block;
        background: #fff;
        border-radius: 3px;
        width: 3px;
        height: calc(var(--iw)/2);
      }
      &.active,&.active:after{
        background: #32CCBC;
      }
    }

    .top_left{
      left: -1px;
      top:-1px;
      cursor: nw-resize;
      &:after{
        top:0;
        left:0;
      }
    }
    .top_right{
      right: -1px;
      top:-1px;
      cursor: ne-resize;
      &:after{
        top:0;
        right:0;
      }
    }
    .bottom_left{
      left: -1px;
      bottom:-1px;
      cursor: ne-resize;
      &:after{
        bottom:0;
        left:0;
      }
    }
    .bottom_right{
      right: -1px;
      bottom:-1px;
      cursor: nw-resize;
      &:after{
        bottom:0;
        right:0;
      }
    }

  }
}

