
@import '../../scss/mixin';

.image_preview{
  @include display-block;

  .image_preview_img{
    @include flex-row;
    border: dotted 2px #32CCBC;
    @include user-select-none;
    overflow: hidden;
    // width: max-content;
    max-width: 100%;
    margin: 0 auto;
    img{
      @include user-select-none;
      display: block;
      position: absolute;
      width: 100%;
      top: 50%;
      left:50%;
      transform: translate(-50%,-50%);
    }

    &.image_preview_img_static img{
      position: relative;
      top:0;
      left: 0;
      transform: none;
    }
  }

  .image_preview_options{
    @include flex-row;
    @include justify-start;
    margin-bottom: 10px;
    button{
      $c:#32CCBC;
      @include display-block;
      width: max-content;
      background: #222227;
      padding: 8px;
      border-radius: 6px;
      border: solid 1px #666;
      margin-right: 12px;
      &:last-child{
        margin-right: 0;
      }
      svg{
        display: block;
        height: 18px;
        width: 18px;
        path{
          stroke:#aaa;
          fill:#aaa;
        }
      }
      &:nth-child(2) svg path{
        fill: none;
      }

      &:hover{
        svg path{
          stroke:#fff;
          fill:#fff;
        }
        &:nth-child(2) svg path{
          fill: none;
        }
      }
    }
  }

  .image__crop__tools{
    @include flex-row;
    @include justify-space-between;
    margin-top: 20px;
    flex-wrap: wrap;

    .image__rotate__bar{
      @include flex-row;
      @include justify-start;
      margin-bottom: 10px;
      input{
        @include display-block;
        -webkit-appearance: none;
        height: 5px;
        background: #666;
        flex-shrink: 1;
        &::-webkit-slider-thumb {
          -webkit-appearance: none;
          appearance: none;
          bottom: 100%;
          width: 15px;
          height: 15px;
          border-radius: 50%;
          background: #32CCBC;
          cursor: pointer;
        }
      }
      span{
        @include display-block;
        flex-shrink: 0;
        width: auto;
        min-width: 50px;
        margin-left: 10px;
        text-align: center;
        font: 400 16px/18px 'Inter', sans-serif;
        color: #ccc;
        border: solid 1px #32CCBC;
        border-radius: 6px;
        padding: 4px 8px;
      }
    }

    .image__crop__tool{
      @include flex-row;
      @include justify-start;
      width: auto;
      button{
        $c:#32CCBC;
        @include display-block;
        width: max-content;
        background: #222227;
        padding: 8px;
        border-radius: 6px;
        border: solid 1px #666;
        margin-right: 12px;
        &:last-child{
          margin-right: 0;
        }
        svg{
          display: block;
          height: 18px;
          width: 18px;
          path{
            fill:#ccc;
            &.first-flip{
              fill:#aaa;
            }
            &.second-flip{
              fill:#555;
            }
          }
        }

        &:hover{
          svg path{
            fill:#fff;
          }
        }
        &.dark-icon {
          svg path{
            fill:#999;
          }

          &.ic-90 svg{
            transform: rotate(90deg);
          }
          &.ic-180 svg{
            transform: rotate(180deg);
          }
          &.ic-270 svg{
            transform: rotate(270deg);
          }
        }
        &.active svg path.first-flip{
          fill:#32CCBC;
        }
      }
    }

    .image__crop__finish{
      @include flex-row;
      @include justify-start;
      width: auto;
      button{
        @include display-block;
        width: max-content;
        background: #222227;
        padding: 3px;
        border-radius: 6px;
        border: solid 1px #666;
        margin-right: 12px;
        &:last-child{
          margin-right: 0;
        }
        svg{
          display: block;
          height: 22px;
          width: 22px;
        }
        &:first-child svg{
          fill:#aaa;
        }
        &:last-child svg path{
          stroke:#aaa;
        }
        &:hover{
          &:first-child svg{
            fill:#fff;
          }
          &:last-child svg path{
            stroke:#fff;
          }
        }
      }
    }
  }

  @media(max-width:768px){

    .image_preview_options{
      button{
        padding: 6px;
        border-radius: 4px;
        margin-right: 8px;
        svg{
          height: 14px;
          width: 14px;
        }
      }
    }

    .image__crop__tools{
      margin-top: 10px;

      .image__rotate__bar{
        margin-bottom: 10px;
        span{
          margin-left: 6px;
          font: 400 14px/16px 'Inter', sans-serif;
          border-radius: 4px;
          padding: 4px 4px;
        }
      }

      .image__crop__tool{
        button{
          padding: 6px;
          border-radius: 4px;
          margin-right: 8px;
          svg{
            height: 14px;
            width: 14px;
          }
        }
      }

      .image__crop__finish{
        button{
          padding: 3px;
          border-radius: 4px;
          margin-right: 8px;
          svg{
            height: 16px;
            width: 16px;
          }
        }
      }
    }
  }

}

@keyframes loader-fram {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.process__loader{
  @include flex-row;
  position: absolute;
  content: "";
  inset: 0;
  background: rgba(0,0,0,.3);
  z-index: 9;

  &:after{
    @include display-block;
    content: "";
    border: 4px solid #888;
    border-top: 4px solid #ccc;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: loader-fram 2s linear infinite;
  }
}
