
@import '../../scss/mixin';


.image__filter__options{
  @include display-block;

  .css__filters{
    @include display-block;
    margin-top: 40px;

    .css__filters__ranges{
      @include display-block;
      margin-top: 20px;
      background: #222227;
      padding: 18px 8px 12px;
      border-radius: 8px 8px 0 0;

      .filter__range{
        @include flex-row;
        margin-bottom: 20px;
        &:last-child{
          margin-bottom: 0;
        }
        p{
          @include display-block;
          width: 120px;
          font: 400 16px/18px 'Inter', sans-serif;
          color: #ccc;
          flex-shrink: 0;
        }
        input{
          @include display-block;
          -webkit-appearance: none;
          height: 14px;
          flex-shrink: 1;
          border: 1px solid #555;
          background: #444;

          --w :0%;
          background: linear-gradient(to right,#32CCBC var(--w),#444 var(--w));

          &::-webkit-slider-thumb {
            -webkit-appearance: none;
            appearance: none;
            width: 12px;
            height: 12px;
            background: #000;
            border: 3px solid #32CCBC;
            cursor: pointer;
          }
        }
        span{
          @include display-block;
          position: absolute;
          display: none;
          width: auto;
          bottom: 100%;
          right: 0;
          font: 400 12px/14px 'Inter', sans-serif;
          color: #fff;
        }
        input:focus + span{
          display: block;
        }
      }
    }


    .css__filters__options{
      @include flex-row;
      @include justify-start;
      @include scroll-bar3;
      margin: 20px 0 8px;
      background: #222227;
      padding: 10px 8px 10px;
      border-radius: 8px;
      overflow: auto;

      .css__filters__option{
        @include display-block;
        width: 40px;
        height: 40px;
        background-color: #444;
        border-radius: 5px;
        margin-right: 12px;
        flex-shrink: 0;
        &:last-child{
          margin-right: 0;
        }
        &:hover{
          border-color: #aaa;
        }
        &.active{
          border-color: #32CCBC;
        }
      }
    }

    .css__filters__body{
      @include flex-row;
      @include justify-start;
      @include scroll-bar3(8px);
      background: #222227;
      padding: 8px;
      border-radius: 8px;
      overflow: auto;

      &.active{
        border-radius: 0 0 8px 8px;
      }

      .css__filter{
        @include display-block;
        width: auto;
        margin-right: 10px;
        background: #222227;
        padding: 5px;
        border: solid 1px #444;
        border-radius: 5px;
        cursor: pointer;
        &:hover{
          background: #444;
          p{color: #fff;}
        }
        &.active{
          border-color: #32CCBC;
          p{color: #fff;}
        }
        &:last-child{
          margin-right: 0;
        }

        .css__filter__setting{
          @include flex-row;
          position: absolute;
          width: 60px;
          height: 60px;
          z-index: 1;
          background: rgba(0,0,0,.3);

          svg{
            width: 30px;
            height: 30px;
            path{
              stroke : #ccc;
            }
          }
        }

        img{
          @include display-block;
          object-fit: contain;
          width: 60px;
          height: auto;
        }
        .img{
          @include display-block;
          background-color: #444;
          width: 60px;
          height: 60px;
        }
        p{
          @include display-block;
          @include user-select-none;
          font: 400 14px/16px 'Inter', sans-serif;
          color: #ccc;
          margin-top: 5px;
          text-align: center;
        }
        span{
          font: 400 10px/12px 'Inter', sans-serif;
        }
      }
    }
  }




  @media(max-width:992px){
    margin-top: 30px;
    padding: 0;
  }

  @media(max-width:768px){
    margin-top: 20px;

    .css__filters{

      .css__filters__ranges{
        margin-top: 10px;

        .filter__range{
          margin-bottom: 15px;
          p{
            width: 90px;
            font: 400 14px/16px 'Inter', sans-serif;
          }
          input{
            height: 12px;
            &::-webkit-slider-thumb {
              width: 10px;
              height: 10px;
              border: 2px solid #32CCBC;
            }
          }
        }
      }
    }
  }

}
