
@import '../../scss/mixin';



.edit__type__menu{
  @include flex-row;
  @include justify-start;
  border: solid 2px #333;
  padding: 2px;
  border-radius: 8px;
  margin-top: 45px;

  .edit__type__menu__item{
    @include flex-column;
    @include justify-center;
    @include align-center;
    background: #333;
    margin-right: 2px;
    padding: 8px 5px;
    cursor: pointer;

    &:first-child{
      border-radius: 6px 0 0 6px;
    }
    &:last-child{
      margin-right: 0;
      border-radius: 0 6px 6px 0;
    }
    span{
      @include display-block;
      width: auto;
      svg{
        height: 20px;
        width: auto;
        path{
          stroke: #ccc;
        }
        &.path-style path{fill:#ccc; stroke: none;}

      }
    }
    p{
      @include display-block;
      width: auto;
      color: #ccc;
      font: 400 14px/16px 'Inter', sans-serif;
    }

    &:hover{
      background: #555;
      span svg{
        path{
          stroke: #fff;
        }
        &.path-style path{fill:#fff; stroke: none;}
      }
      p{
        color: #fff;
      }
    }

    &.active{
      span svg{
        path{
          stroke: #32CCBC;
        }
        &.path-style path{fill:#32CCBC; stroke: none;}
      }
      p{
        color: #32CCBC;
      }
    }
  }

  @media(max-width:992px){
    margin-top: 30px;
  }
  @media(max-width:768px){
    margin-top: 20px;
  }
}


.image__edit__options{
  @include display-block;
  margin-top: 40px;
  // padding-left: 40px;


  .image__edit__option{
    @include flex-row;
    @include justify-start;
    margin-bottom: 30px;

    &:last-child{
      margin-bottom: 0;
    }

    .image__edit__option__title{
      @include flex-row;
      @include justify-space-between;
      width: 90px;
      font: 500 20px/24px 'Inter', sans-serif;
      color: #ccc;
      margin-right: 20px;
      flex-shrink: 0;
    }

    .image__edit__option__input{
      @include flex-row;
      width: auto;
      border: solid 1px #32CCBC;
      border-radius: 6px;
      padding: 6px 10px;
      .input-sm,.input-md{
        @include display-block;
        width: 40px;
        font: 400 20px/24px 'Inter', sans-serif;
        background: transparent;
        color: #ccc;
        padding-right: 2px;
      }
      .input-md{
        width: 55px;
      }
      span{
        @include display-block;
        width: auto;
        font: 400 18px/24px 'Inter', sans-serif;
        color: #ccc;
      }
    }
    .image__edit__option__crops{
      @include flex-row;
      @include justify-start;
      flex-wrap: nowrap;
      .crops__each{
        @include flex-column;
        @include align-center;
        // width: auto;
        margin-right: 10px;
        border: solid 1px #32CCBC;
        border-radius: 6px;
        padding: 8px;
        width: 20%;
        cursor: pointer;
        flex-shrink: 1;
        &:last-child{
          margin-right: 0;
        }
        &:hover{background: #222227;}
        span{
          @include flex-row;
          height: 24px;
          width: 24px;
          &:after{
            content: "";
            display: block;
            position: relative;
            height: 20px;
            width: 20px;
            border: solid 2px #aaa;
          }
          &.c-custom:after{border-style: dotted;}
          &.c-11:after{width: 16px;height: 16px;}
          &.c-34:after{width: 14px;}
          &.c-43:after{height: 14px;}
          &.c-23:after{width: 12px;}
          &.c-32:after{height: 12px;}
        }
        p{
          @include display-block;
          font: 400 12px/14px 'Inter', sans-serif;
          text-align: center;
          color: #ccc;
          margin-top: 8px;
        }
        &.active{
          span:after{
            border-color: #fff;
          }
          p{
            color: #fff;
          }
        }

      }
    }

    .image__edit__option__input-divider{
      @include flex-row;
      width: 80px;
      &:after{
        position: absolute;
        z-index: -1;
        content: "";
        width: 60px;
        height: 2px;
        background: #444;
      }
      svg{
        @include flex-row;
        width: 26px;
        height: auto;
        background: #333;
        padding: 5px;
        border-radius: 50%;
        path{
          fill: #32CCBC;
        }
      }
    }

    .image__edit__option__select{
      @include display-block;
      width: auto;

      .select{
        @include display-block;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none; /* Standard syntax */
        transition: color .4s ease;
        &.active{
          .select-head svg{
            transform: rotate(180deg);
          }
        }

        .select-head{
          @include flex-row;
          border: solid 1px #32CCBC;
          border-radius: 6px;
          color: #ccc;
          padding: 6px 10px;
          overflow:hidden;
          cursor: pointer;

          .select-selected{
            @include display-block;
            font: 400 20px/24px 'Inter', sans-serif;
          }
          .select-toggle{
            @include display-block;
            margin-left: 8px;
            svg{
              width: 16px;
              height: auto;
              fill: #ccc;
            }
          }
          &:hover{
            color: #fff;
            svg{
              fill:#fff;
            }
          }
        }
        .select-options{
          @include display-block;
          position: absolute;
          top:calc(100% + 6px);
          left: 0;
          border: solid 1px #32CCBC;
          border-radius: 6px;
          z-index: 2;
          .select-option{
            @include display-block;
            color: #ccc;
            background: #16151A;
            font: 400 18px/22px 'Inter', sans-serif;
            margin-bottom: 1px;
            padding: 4px 10px;
            cursor: pointer;
            border-bottom: solid 1px #32CCBC;
            &:first-child{
              border-radius: 6px 6px 0 0;
            }
            &:last-child{
              margin-bottom: 0;
              border:none;
              border-radius: 0 0 6px 6px;
            }
            &:hover{
              color: #fff;
            }
          }
        }
      }
    }

  }


  @media(max-width:992px){
    // margin-top: 30px;
    // padding-left: 0;

  }

  @media(max-width:768px){
    // margin-top: 20px;

    .image__edit__option{
      margin-bottom: 20px;

      .image__edit__option__title{
        width: 80px;
        font: 500 18px/22px 'Inter', sans-serif;
        margin-right: 15px;
      }

      .image__edit__option__input{
        border-radius: 4px;
        padding: 4px 6px;
        .input-sm,.input-md{
          width: 38px;
          font: 400 16px/22px 'Inter', sans-serif;
        }
        .input-md{
          width: 45px;
        }
        span{
          font: 400 16px/22px 'Inter', sans-serif;
        }
      }

      .image__edit__option__crops{
        .crops__each{
          margin-right: 10px;
          border-radius: 4px;
          padding: 6px;
          span{
            height: 18px;
            width: 18px;
            &:after{
              height: 14px;
              width: 14px;
              border: solid 2px #ccc;
            }
            &.c-custom:after{border-style: dotted;}
            &.c-11:after{width: 14px;height: 14px;}
            &.c-34:after{width: 9.5px;}
            &.c-43:after{height: 9.5px;}
            &.c-23:after{width: 8px;}
            &.c-32:after{height: 8px;}
          }
          p{
            font: 500 10px/14px 'Inter', sans-serif;
            margin-top: 4px;
          }
        }
      }

      .image__edit__option__input-divider{
        width: 65px;
        &:after{
          width: 50px;
        }
        svg{
          width: 22px;
          padding: 3px;
        }
      }

      .image__edit__option__select{
        .select{

          .select-head{
            border-radius: 4px;
            padding: 4px 6px;

            .select-selected{
              font: 400 16px/22px 'Inter', sans-serif;
            }
            .select-toggle{
              margin-left: 5px;
              svg{
                width: 14px;
              }
            }
          }
          .select-options{
            border-radius: 4px;
            .select-option{
              font: 400 16px/22px 'Inter', sans-serif;
              padding: 4px 6px;
              &:first-child{
                border-radius: 4px 4px 0 0;
              }
              &:last-child{
                border-radius: 0 0 4px 4px;
              }
            }
          }
        }
      }

    }

  }
}
