@import '../../scss/mixin';

/***********************Start home__banner********************************/

.home__container{
  @include flex-row;
  @include justify-start;
  flex-wrap: wrap;
  padding-top: 50px;


  .home__btn{
    @include flex-row;
    width: 22%;
    margin: 0 4% 35px 0;
    &:nth-child(4n){
      margin-right: 0;
    }

    a{
      @include flex-row;
      --color:  #fff;
      --duration: .4s;
      --bg: #32CCBC;
      --bg2: #90F7EC;

      cursor: pointer;
      color: var(--color);
      background-color: var(--bg);
      font: 400 18px/22px 'Inter', sans-serif;
      transition: var(--duration) ease;
      padding: 15px 30px;
      text-decoration: none;
      &::before {
        content: "";
        position: absolute;
        inset: 2px;
        background: #222227;
      }

      span {
        position: relative;
        z-index: 1;
      }
      i {
        position: absolute;
        inset: 0;
        display: block;
        &::before{
          content: "";
          position: absolute;
          width: 20px;
          height: 6px;
          right: 10px;
          top: -4px;
          border: 2px solid var(--bg2);
          background: #222227;
          transition: right var(--duration);
        }
        &::after{
          content: "";
          position: absolute;
          width: 20px;
          height: 6px;
          left: 10px;
          bottom: -4px;
          border: 2px solid var(--bg2);
          background: #222227;
          transition: left var(--duration);
        }
      }

      &:hover {
        i::before {
          width: 25px;
          right: calc(100% - 35px);
        }
        i::after {
          width: 25px;
          left: calc(100% - 35px);
        }
      }
    }
  }

   @media(max-width:992px){
     .home__btn{
       width: 30%;
       margin: 0 5% 35px 0;
       &:nth-child(4n){
         margin-right: auto;
       }
       &:nth-child(3n){
         margin-right: 0;
       }
     }
   }

   @media(max-width:768px){
     @include justify-space-between;

     .home__btn{
       width: 47%;
       margin: 0 0 35px;
       &:nth-child(3n),&:nth-child(4n){
         margin-right: 0;
       }
     }
   }
   @media(max-width:576px){
     .home__btn{
       width: 100%;
       &:last-child{
         margin: 0;
       }
     }
   }
}
/***********************End home__banner********************************/
