
@import '../../scss/mixin';

.pdf__edit__options{
  @include display-block;


  .pdf__edit__option{
    @include flex-row;
    @include justify-start;
    margin-bottom: 30px;

    &:last-child{
      margin-bottom: 0;
    }

    .pdf__edit__option__title{
      @include flex-row;
      @include justify-space-between;
      width: 140px;
      font: 500 20px/24px 'Inter', sans-serif;
      color: #ccc;
      margin-right: 20px;
      flex-shrink: 0;
    }

    .pdf__edit__option__input{
      @include flex-row;
      width: auto;
      border: solid 1px #32CCBC;
      border-radius: 6px;
      padding: 6px 10px;
      input{
        @include display-block;
        width: 40px;
        font: 400 20px/24px 'Inter', sans-serif;
        background: transparent;
        color: #ccc;
        padding-right: 2px;
      }
      span{
        @include display-block;
        width: auto;
        font: 400 18px/24px 'Inter', sans-serif;
        color: #ccc;
      }
    }

    .pdf__edit__option__range{
      @include display-block;
      width: 50%;
      input{
        @include display-block;
        -webkit-appearance: none;
        height: 5px;
        background: #666;
        flex-shrink: 1;
        &::-webkit-slider-thumb {
          -webkit-appearance: none;
          appearance: none;
          bottom: 100%;
          width: 15px;
          height: 15px;
          border-radius: 50%;
          background: #32CCBC;
          cursor: pointer;
        }
      }
    }

    .pdf__edit__option__select{
      @include display-block;
      width: max-content;

      .select{
        @include display-block;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none; /* Standard syntax */
        transition: color .4s ease;
        &.active{
          .select-head svg{
            transform: rotate(180deg);
          }
        }

        .select-head{
          @include flex-row;
          @include justify-space-between;

          border: solid 1px #32CCBC;
          border-radius: 6px;
          color: #ccc;
          padding: 6px 10px;
          overflow:hidden;
          cursor: pointer;

          .select-selected{
            @include display-block;
            font: 400 20px/24px 'Inter', sans-serif;
            white-space:pre-wrap;
          }
          .select-toggle{
            @include display-block;
            margin-left: 8px;
            width: 16px;

            svg{
              display: block;
              width: 100%;
              height: auto;
              fill: #ccc;
            }
          }
          &:hover{
            color: #fff;
            svg{
              fill:#fff;
            }
          }
        }
        .select-options{
          @include display-block;
          position: absolute;
          top:calc(100% + 6px);
          left: 0;
          border: solid 1px #32CCBC;
          border-radius: 6px;
          z-index: 2;
          .select-option{
            @include display-block;
            color: #ccc;
            background: #16151A;
            font: 400 18px/22px 'Inter', sans-serif;
            padding: 4px 10px;
            cursor: pointer;
            border-bottom: solid 1px #32CCBC;
            &:first-child{
              border-radius: 6px 6px 0 0;
            }
            &:last-child{
              margin-bottom: 0;
              border:none;
              border-radius: 0 0 6px 6px;
            }
            &:hover{
              color: #fff;
            }
          }
        }
      }
    }
  }

  @media(max-width:992px){
    margin-top: 30px;
  }
  @media(max-width:768px){
    margin-top: 20px;

    .pdf__edit__option{
      margin-bottom: 20px;

      .pdf__edit__option__title{
        width: 115px;
        font: 500 16px/20px 'Inter', sans-serif;
        margin-right: 15px;
      }

      .pdf__edit__option__input{
        border-radius: 4px;
        padding: 4px 6px;
        input{
          width: 38px;
          font: 400 16px/22px 'Inter', sans-serif;
        }
        span{
          font: 400 16px/22px 'Inter', sans-serif;
        }
      }

      .pdf__edit__option__select{
        .select{

          .select-head{
            border-radius: 4px;
            padding: 4px 6px;

            .select-selected{
              font: 400 16px/22px 'Inter', sans-serif;
            }
            .select-toggle{
              margin-left: 5px;
              width: 14px;
            }
          }
          .select-options{
            border-radius: 4px;
            .select-option{
              font: 400 16px/22px 'Inter', sans-serif;
              padding: 4px 6px;
              &:first-child{
                border-radius: 4px 4px 0 0;
              }
              &:last-child{
                border-radius: 0 0 4px 4px;
              }
            }
          }
        }
      }

    }

  }
}
