@import '../../scss/mixin';


.download__btn{
  @include flex-row;
  width: 50%;
  border: solid 2px #32CCBC;
  border-radius: 8px;
  background: #222227;
  color: #ccc;
  font: 500 22px/26px 'Inter', sans-serif;
  padding: 15px 10px;
  margin-top: 40px;

  &:hover{
    color: #fff;
  }

  @media(max-width:768px){
    width: 100%;
    margin-top: 30px;
    font: 500 18px/22px 'Inter', sans-serif;
    padding: 12px 10px;
  }
}
