@import '../../scss/mixin';


.pdf__preview__Container{
  @include display-block;
  width: 50%;

  .pdf__reset{
    @include display-block;
    width: auto;
    background: #222227;
    border: solid 1px #888;
    border-radius: 6px;
    margin-bottom: 10px;
    padding: 6px;

    svg{
      display: block;
      width: 30px;
      height: auto;
      path{
        stroke:#aaa;
      }
    }
    &:hover{
      border-color: #32CCBC;
      svg path{stroke: #32CCBC;}
    }
  }

  .pdf__pages{
    @include display-block;
    border: solid 2px #32CCBC;
    padding: 10px;

    .pdf__page{
      @include display-block;
      border: solid 2px #444;
      margin-bottom: 10px;
      &:last-child{
        margin-bottom: 0;
      }
      img{
        @include display-block;
        object-fit: contain;
      }
      button{
        position: absolute;
        display: block;
        z-index: 1;
        top: 50%;
        left:50%;
        transform: translate(-50%,-50%);
        padding: 15px 20px;
        background: #222227;
        border: solid 1px #32CCBC;
        border-radius: 6px;
        color: #ccc;
        font: 500 22px/24px 'Inter', sans-serif;
        &:hover{
          color: #fff;
        }
      }
    }
  }
  @media(max-width:1200px){
    width: 70%;
  }
  @media(max-width:992px){
    width: 80%;
    .pdf__pages{
      padding: 8px;
      .pdf__page{
        margin-bottom: 8px;
      }
    }
  }
  @media(max-width:768px){
    width: 100%;
    .pdf__pages{
      padding: 5px;
      .pdf__page{
        margin-bottom: 5px;
        button{
          padding: 10px 15px;
          font: 500 18px/22px 'Inter', sans-serif;
        }
      }
    }
  }
}
